<template>
    <div class="artists-list">
        <button @click="collapse" class="collapse-button">{{ collapsed ? '+' : '-' }}</button>
        <h2>Artists:</h2>
        <ol v-if="!collapsed">
            <li v-for='(artist, i) of artists' :key='i' :artist='artist'>
                <span class="artist-name">{{ i+1 }} - 
                    <a :href="'https://www.last.fm/music/'+artist.replace(/ /g,'+')">{{ artist }}</a>
                </span>
                ({{ listens[artist] }} {{ listens[artist]>1 ? 'listens' : 'listen' }}){{ i == artists.length-1 ? '' : ', '}}</li>
        </ol>
    </div>
</template>

<script>
export default {
    props: ['artists','listens'],
    data:function(){return{
        collapsed:true,
    }},
    methods: {
        collapse: function(){
            this.collapsed = !this.collapsed
        },
    },
}
</script>

